hr{
    border:0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.container-box{
    width: 80%;
    background-color: transparent;
    position: relative;
    z-index: 9;
    margin-top: 0em;
    size: cover;
}

.particle-canvas{
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

